  width: 100%;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  color: #757575;
}

.form__select {
  background: #fff;
}

.form__select--is-open .form__checkbox {
  visibility: visible;
  opacity: 1;
  animation: is-open 1s ease forwards 0s;
}

.form__checkbox {
  visibility: hidden;
  opacity: 0;
  animation: is-close 1s ease forwards 0s;
}

.form__info {
  font-size: 11px;
  text-align: justify;
}

.form__info--color {
  color: #428bca;
}

.form__button {
  margin-top: 20px;
  background: #1d87b5;
  color: #fff;
  padding: 10px;
  border-radius: 3px;
  font-size: 13px;
}

.form__button:hover {
  background: rgba(29, 135, 181, 0.9);
}

.footer__select-title {
  position: relative;
  cursor: pointer;
}

.footer__select-title:after {
  display: block;
  content: '';
  position: absolute;
  top: 5px;
  right: 20px;
  width: 10px;
  height: 10px;
  border-right: 2px solid #75758b;
  border-top: 2px solid #75758b;
  transform: rotate(135deg);
}

.checkbox {
  margin-right: 10px;
  cursor: pointer;
}

.checkbox__label {
  display: block;
  font-size: 16px;
  padding: 3px;
  cursor: pointer;
}

@keyframes is-open {
  0% {
    opacity: 0;
    height: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    height: 360px;
  }
}

@keyframes is-close {
  0% {
    opacity: 1;
    height: 360px;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    height: 0;
  }
}
